<template>
  <div v-show="popShow" class="seat-pop-box" :style="sty">
    <ul>
      <li v-if="!switchNumSet" @mousedown.left="popDialog"></li>
      <li v-else @mousedown.left="popDialog"></li>
    </ul>

    <el-dialog :visible.sync="dialogVisible" title="" width="300px" :modal="false" :append-to-body="true">
      <el-form :model="form" size="small">
        <template v-if="switchNumSet">
          <el-form-item label="">
            <el-radio-group v-model="form.orderPosition">
              <el-radio label="lt"></el-radio>
              <el-radio label="rt"></el-radio>
              <el-radio label="rb"></el-radio>
              <el-radio label="lb"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-input-number v-model="form.orderBegin" :precision="0" :controls="false" :min="1"></el-input-number>
          </el-form-item>
        </template>
        
        <template v-else>
          <el-form-item label="">
            <el-input-number v-model="form.colNum" :precision="0" :controls="false" :min="1"></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-input-number v-model="form.total" :precision="0" :controls="false" :min="1"></el-input-number>
          </el-form-item>
        </template>
  
        <el-form-item style="text-align: right">
          <el-button v-if="!switchNumSet" @click="setGroup"></el-button>
          <el-button v-else @click="quickOrder"></el-button>
        </el-form-item>
        
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/designer/seat/constant";

export default {
  name: "SeatPop",
  data(){
    return{
      dialogVisible: false,
      form: {
        colNum: undefined,
        total: undefined,
        orderPosition: 'lt',
        orderBegin: 1
      },
      seat: {},
      position: {},
      popShow: false
    }
  },
  computed:{
    ...mapState('seatDesign',[
      "switchNumSet"
    ]),
    sty(){
      return{
        left: this.position.x + "px",
        top: this.position.y + "px"
      }
    }
  },
  mounted(){
    eventBus.on(eventBusEvent.SEAT_POP, data => {
      if (data.seat.type !== "group" && this.switchNumSet) {
        this.popShow = false;
        return
      }
      Object.assign(this, data);    //this.seat 
      this.form.orderBegin = data.maxOrderNum+1;
      this.popShow = true;
      if (data.seat.type === "group"){
        this.form.colNum = data.seat.cols;
        this.form.total = data.seat.member.length
      }
      else {
        this.form.colNum = undefined;
        this.form.total = undefined;
      }
       
    })

    eventBus.listen(eventBusEvent.BODY_EMPTY_CLICK, ()=>{
      this.popShow = false;
    })
  },
  methods:{
    popDialog(evt){
      evt.stopPropagation();
      evt.preventDefault();
      this.popShow = true;
      this.dialogVisible = true
    },
    setGroup(){
      if (this.seat.type !== "group"){
        this.setGroupBySeat()
      }
      else {
        this.setGroupByGroup()
      }
    },
    setGroupBySeat(){
      let {total, colNum} = this.form;
      if (!total || !colNum || total === 1){
        this.dialogVisible = false;
        this.popShow = false;
        return false
      }
      let rowNum = Math.ceil(total/colNum);
      let parentId = dsf.uuid();
      this.seat.parentId = parentId;
      this.seat.belong = "group";
      const memberList = getMemberList.call(this, colNum, total, this.seat)
      let _width = this.seat.width * colNum;
      let _height = this.seat.height * rowNum;

      const group = {
        ...this.seat,
        id: parentId,
        type: 'group',
        cols: colNum,
        rows: rowNum,
        width: _width,
        height: _height,
        memberType: this.seat.type,
        member: memberList,
        isFrozenSty: false,
        isFrozenMemberSty: false
      }
      this.$store.commit("seatDesign/addGroup2Panel", group);
      eventBus.emit(eventBusEvent.KEY_DELETE)
      this.dialogVisible = false;
      this.popShow = false;
    },
    setGroupByGroup(){
      let {total, colNum} = this.form;
      let rowNum = Math.ceil(total/colNum);
      const one = this.seat.member[0]
      one.parentId = null;
      one.belong = null;
      if (total === 1){
        const seat = {
          ...one,
          left: this.seat.left,
          top: this.seat.top,
          angle: this.seat.angle,
          stateList: this.seat.stateList,
          isFrozenSty: false,
        }
        this.$store.commit("seatDesign/addSeat2Panel", seat);
      }
      else if (total > 1){
        let parentId = dsf.uuid();
        one.parentId = parentId;
        one.belong = "group";
        const memberList = getMemberList.call(this, colNum, total, one);
        let _width = one.width * colNum;
        let _height = one.height * rowNum;
        const group = {
          ...this.seat,
          id: parentId,
          cols: colNum,
          rows: rowNum,
          width: _width,
          height: _height,
          member: memberList,
          isFrozenSty: false,
          isFrozenMemberSty: false
        }
        this.$store.commit("seatDesign/addGroup2Panel", group);
      }
      eventBus.emit(eventBusEvent.KEY_DELETE);
      this.dialogVisible = false;
      this.popShow = false;
    },
    quickOrder(){
      if (this.form.orderPosition === "lt"){
        this.seat.member.forEach(a => {
          if (a.seatNum == a.seatTxt) a.seatTxt = this.form.orderBegin;
          a.seatNum = this.form.orderBegin;
          this.form.orderBegin +=1
        })
      }
      eventBus.emit(eventBusEvent.AFTER_QUICK_ORDER);
      this.dialogVisible = false;
      this.popShow = false;
    }
  }
}
  
function getMemberList(colNum, total, item) {
  const memberList = [];
  for (let i=0; i< total; i++){
    let rowIndex = Math.floor(i / colNum);
    let colIndex = (i % colNum);
    let _left = colIndex * item.width;
    let _top = rowIndex * item.height;
    
    let member = {
      ...item,
      id: dsf.uuid(),
      left: _left,
      top: _top,
      colIndex: colIndex,
      rowIndex: rowIndex,
      angle: 0
    }
    
    memberList.push(member)
  }
  return memberList;
}
</script>