<template>
  <div class="designer_tools">
    <DsfButton
      v-for="(button, $index) in layout.buttons"
      :key="$index"
      :icon="button.icon"
      :text="button.text"
      btn-style="icon-text"
      @click="button.handler($event, button)"/>
    <width-input :canvas-set="canvasSet" :has-bg="hasBg"></width-input>
  </div>
</template>

<script>
import {createUploadFileInput, uploadImg} from '../func'
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/designer/seat/constant";
import {mapState} from 'vuex';
import widthInput from "_platform/designer/seat/seatDesign/widthInput";
  
export default {
  name: "TopTools",
  props: {
    //
    items: {
      type: Array,
      default() {
        return []
      }
    },
    //
    groups: {
      type: Array,
      default() {
        return []
      }
    },
      
    backgroundImage: {
      type: Object,
      default() {
        return {
          url: "",
          width: 0,
          height: 0,
        }
      }
    },
    canvasSet:{
      type: Object,
      default() {
        return {
          width: undefined,
          height: undefined,
        }
      }
    },
    hasBg: {
      type: Boolean,
      default: false
    },   //
  },
  components: {
    widthInput,
  },
  data() {
    return {
      //
      normalButtons: [
        {text: "", icon: "baocun", handler: this.saveLayout},
        {text: "", icon: "shuzishurukuang", handler: this.setNum},
        {text: "", icon: "tupiantianjia", handler: this.updateBG},
        {text: "", icon: "shuaxin", handler: this.restoreLayout},
        {text: "", icon: "shangyibu1", handler: this.backStep},
      ],
      numButtons: [
        {text: "", icon: "dui1", handler: this.numSave},
        {text: "", icon: "icon2", handler: this.numCancel},
        {text: "", icon: "shangyibu1", handler: this.backStep},
        {text: "", icon: "shanchu", handler: this.numClean}
      ],
      layout: {
        buttons: [],
      },
    }
  },
    
  computed: {
    ...mapState('seatDesign', [
      "compelPanelSize",
      "switchNumSet",
      "haveSelectedItems"
    ])
  },
  watch: {
    "canvasSet.width"(val) {
      if (!this.hasBg) return;
      if (!val) {
        this.$nextTick(() => {
          let boxWid = $(".seat-designer-page").width();
          let scale = boxWid / this.backgroundImage.width;
          this.canvasSet.height = this.backgroundImage.height * scale;
        });
        return;
      }
      let scale = val / this.backgroundImage.width;
      this.canvasSet.height = this.backgroundImage.height * scale;
    },
    "switchNumSet"(val) {
      if (val) {
        this.layout.buttons = this.numButtons
      } else {
        this.layout.buttons = this.normalButtons
      }
    },
    "haveSelectedItems"(val) {
      const arr = [
        {text: "", icon: "dui1", handler: this.numSave},
        {text: "", icon: "icon2", handler: this.numCancel},
        {text: "", icon: "shangyibu1", handler: this.backStep},
      ];
      let temp;
      if (val) {
        temp = [
          {text: "", icon: "kongjianweixuan", handler: this.numClear}
        ]
      } else {
        temp = [
          {text: "", icon: "shanchu", handler: this.numClean}
        ]
      }
      this.numButtons = arr.concat(temp)
      if (this.switchNumSet) {
        this.layout.buttons = arr.concat(temp);
      }
    }
  },
  created() {
    this.layout.buttons = this.normalButtons;
  },
  methods: {
    backStep() {
    },
      
    setNum() {
      this.$store.commit("seatDesign/changeSwitchNumSet", true);
    },
    numSave() {
      this.$store.commit("seatDesign/changeSwitchNumSet", false);
    },
    numCancel() {
      this.$store.commit("seatDesign/changeSwitchNumSet", false);
    },
    numClean() {
      eventBus.emit(eventBusEvent.CLEAN_ALL_SEAT_NUM);
    },
    numClear() {
      eventBus.emit(eventBusEvent.CLEAN_SELECT_SEAT_NUM)
    },
    saveLayout() {
      const numObj = {}
      const judgeNum = it => {
        if (!it.seatNum) {
          dsf.layer.message("", false)
          return false
        }
        if (numObj[it.seatNum]) {
          dsf.layer.message("", false)
          return false
        }
        numObj[it.seatNum] = it.seatNum;
        return true
      }
      for (let it of this.items) {
        if (/^sec/.test(it.type)) continue;
        if (!judgeNum(it)) return;
      }
      for (let it of this.groups) {
        for (let m of it.member) {
          if (!judgeNum(m)) return;
        }
      }
      this.$emit("save-layout");
    },
    restoreLayout() {
      const panelSize = {
        width: this.compelPanelSize.width,
        height: this.compelPanelSize.height,
        scale: 1,
        left: 0,
        top: 0,
      }
      this.$store.commit("seatDesign/changePanelSize", panelSize);
    },
    updateBG() {
      const self = this;
      let upDOM = createUploadFileInput("dsf-seat-bg-upload");
      $(upDOM).on('change', function () {
        if (!upDOM.files || upDOM.files.length === 0) return false;
        const formData = new FormData();
        formData.append("file", upDOM.files[0]);
        const callback = result => {
          //input
          $(upDOM).val("");
          $(upDOM).remove();
          upDOM = null;
            
          if (result === "error") {
            self.$emit("update:hasBg", false);
            return false;
          }
          setBgImage2Panel.call(self, result[0]);
        };
        uploadImg.call(self, formData, callback)
      })
      $(upDOM).click();
    }
  }
}
  
function setBgImage2Panel(data) {
  const self = this;
  let image = new Image();
  image.src = dsf.url.getWebPath(data.relativePath);
  image.onload = function () {
    self.$emit("update:hasBg", true);
    self.$emit("update:backgroundImage", {
      width: image.naturalWidth,
      height: image.naturalHeight,
      relativePath: data.relativePath,
      absolutePath: data.absolutePath
    })
  };
  image.onerror = function () {
    dsf.layer.message("", false);
    self.$emit("update:hasBg", false);
  }
}

</script>